import React from "react";
import Layout from "../../Layout/index";
import Logo from '../../sticker1.svg';
import { Helmet } from "react-helmet";

import './index.scss'

const Main = () => {
    const block = 'b-main';

    return (
        <Layout
            component={
                <div className={block}>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <title> jsx- </title>
                        <link rel="canonical" href="https://rethenz.github.io/" />
                    </Helmet>

                    <img src={Logo} className="App-logo" alt="logo" />
                    <h1> !</h1>
                </div>
            }
        />
    )
}

export default Main;