import React from "react";
import { Helmet } from "react-helmet";

import Layout from "../../Layout/index";
import Logo from '../../sticker.svg';
import './index.scss';

const Contacts = () => {
    const block = 'b-contacts';
    
    return (
        <Layout
            component={
                <div className={block}>
                    <Helmet>
                        <meta charSet="utf-8" />
                        <title> </title>
                        <link rel="canonical" href="https://rethenz.github.io/contacts/" />
                    </Helmet>

                    <img src={Logo} className="App-logo" alt="logo" />
                    <h1>   !</h1>
                </div>
            }
        />
    )
}

export default Contacts;