import React from "react";

import Menu from './menu/index';
import './index.scss';

const Header = () => {
    const block = 'b-header';
    const blockElem = 'b-header__';

    return (
        <header className={block}>
            <div className={`${blockElem}wrap`}>
                <Menu />
            </div>
        </header>
    )
}

export default Header;