import React from "react";
import { Link } from "react-router-dom";

import './index.scss'

const Menu = () => {
    const block = 'b-menu';
    const blockElem = 'b-menu__';

    return (
        <div className={block}>
            <nav className={`${blockElem}nav`}>
                <ul className={`${blockElem}items`}>
                    <li className={`${blockElem}item`}>
                        <Link to="/"></Link>
                    </li>
                    <li className={`${blockElem}item`}>
                        <Link to="/contacts/"> !</Link>
                    </li>
                </ul>
            </nav>
        </div>
    )
}

export default Menu;